/* eslint-disable @typescript-eslint/no-namespace */

import React, { useRef, useEffect } from "react";
import { Canvas, extend, useFrame, useThree } from "@react-three/fiber";
import { shaderMaterial } from "@react-three/drei";
import * as THREE from "three";
import styles from "./background.module.css";

// Remove TypeScript declaration
// declare global {
//   namespace JSX {
//     interface IntrinsicElements {
//       noisyShaderMaterial: any;
//     }
//   }
// }

const NoisyShaderMaterial = shaderMaterial(
  {
    time: 0,
    resolution: new THREE.Vector2(),
    speed: 0.8,
    color1: new THREE.Color(0xff0000), // Red
    color2: new THREE.Color(0x0000ff), // Blue
  },
  // Vertex Shader
  `
      varying vec2 vUv;
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
      }
    `,
  // Fragment Shader
  `
      uniform float time;
      uniform float speed;
      uniform vec2 resolution;
      uniform vec3 color1;
      uniform vec3 color2;
      varying vec2 vUv;
  
      float noise(vec2 p) {
        return fract(sin(dot(p, vec2(900.9898, 540.233))) * 125.5453);
      }
  
      void main() {
        vec2 uv = vUv;
        float n = noise(uv * 1900.0 + time * speed) * 0.8 + 0.2;
        vec3 color = mix(color1, color2, n);
        gl_FragColor = vec4(color, 1.0);
      }
    `
);

// Extend the NoisyShaderMaterial so R3F can use it
extend({ NoisyShaderMaterial });

function Background() {
  const shaderRef = useRef(null);
  const { size } = useThree();

  useEffect(() => {
    if (shaderRef.current) {
      shaderRef.current.uniforms.resolution.value.set(size.width, size.height);
    }
  }, [size]);

  useFrame(({ clock }) => {
    if (shaderRef.current) {
      shaderRef.current.uniforms.time.value = clock.getElapsedTime();
    }
  });

  return (
    <mesh>
      <planeGeometry args={[20, 15]} />
      <noisyShaderMaterial
        ref={shaderRef}
        speed={0.1}
        color1={new THREE.Color(0x0a0a0a)} // Dark gray
        color2={new THREE.Color(0xdbdbdb)} // Light gray
      />
    </mesh>
  );
}

export default function NoisyBackground() {
  return (
    <div className={styles.container}>
      <Canvas>
        <Background />
      </Canvas>
    </div>
  );
}
