import React from "react";
import styles from "./audios.module.css";

export default function Audios() {
  const audios = Array.from({ length: 40 }, (_, i) => i + 1);
  return (
    <div className={styles.audio}>
      {audios.map((audio) => (
        <audio
          key={audio}
          src={`/assets/audios/audio-${audio}.ogg`}
          controls
          className={styles[`audio${audio}`]}
        />
      ))}
    </div>
  );
}
